@import "@/assets/styles/variables.scss";

.list-item {
  position: relative;
}

.lowest-badge {
  padding: 2px 10px;
  position: absolute;
  right: 0;
  top: -25px;
  z-index: 2;

  font-size: 13px;
  background-color: rgba(126, 211, 33, 0.7);
  color: white;
  border-radius: 15px;
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
  box-shadow: -1px 3px 4px 1px #b9b9b963;
}

.span-inline-label {
  margin-right: 4px;
}

.proposal-actions {
  text-align: center;

  .button {
    padding: 0 10px;
    margin-right: 10px;

    cursor: pointer;

    &:last-child {
      margin-right: 0;
    }
  }
}

.success-text {
  color: $apple-green;
  display: block;
  text-align: left;

  .fa-star {
    color: gold;
  }
}

.danger-text {
  color: $danger-color;
}

.container {
  width: 100%;
}

.span-inline-label {
  display: block;
  font-weight: normal;
  font-size: 80%;
  color: $greyish-brown-two;
}
