@import "@/assets/styles/variables.scss";

.error {
  line-height: 16px;
}

.list-item-input,
.list-item-action {
  float: left;
  margin-right: 4%;
  width: 38%;

  line-height: 38px;
}

.list-item-input {
  margin-right: 0;
  position: relative;
  width: 58%;

  .list-item-quantity {
    position: absolute;
    right: 20px;
    top: 0;
  }
}

.max {
  color: $danger-color;
}

.fa-info-circle {
  color: $primary-color;
}

.icon-button {
  padding: 0 12px;
  height: 28px;
  line-height: 28px;
  margin-right: 10px;
}

.input-file {
  float: left;
  max-width: 80%;
}

