@charset "UTF-8";
.overlay.level-1,
.overlay.level-1 > .content {
  z-index: 100; }

.overlay.level-1 > .content > .overlay-btn-close {
  z-index: 101; }

.overlay.level-2,
.overlay.level-2 > .content {
  z-index: 200; }

.overlay.level-2 > .content > .overlay-btn-close {
  z-index: 201; }

.overlay.level-3,
.overlay.level-3 > .content {
  z-index: 300; }

.overlay.level-3 > .content > .overlay-btn-close {
  z-index: 301; }

.overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: #fff; }
  .overlay > .content > .overlay-btn-close {
    height: auto;
    line-height: auto;
    border: 0;
    outline: 0;
    position: absolute;
    top: 0;
    left: 0;
    padding: 12px;
    margin: 0;
    color: #aaa; }
  .overlay > .content {
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    position: relative; }

