@import "@/assets/styles/variables.scss";

.content {
  padding: 12px;
  color: $primary-color;
}

.logo-container {
  font-size: 20px;
  margin-bottom: 20px;
}

.logo {
  max-height: 100px;
  margin-top: 50px;
}

input {
  border: solid 1px #006C4C !important;
}

.button {
  &.button-primary {
    background-color: #006C4C !important;
  }
}

.tool {
  min-height: 100vh;
  background-color: $white;
}

.login-container {
  padding: 0 30px;
}

.terms-container {
  text-align: center;
  line-height: 18px;
}

