@import "@/assets/styles/variables.scss";

.navbar {
  width: 100%;
  height: 56px;
  background-color:#006C4C;//, #62B55A);
}

.navbar-menu {
  height: 100%;
  position: absolute;
  top: 0;
  left: -90%;
  z-index: 10001;
  width: 0;
  overflow: hidden;
  width: 90%;

  color: $charcoal-grey;
  background-color: white;
  transition: 0.2s ease;

  &.open {
    left: 0;
    transition: 0.2s ease;
  }

  ul {
    list-style: none;
    list-style-type: none;
    padding-left: 0;
  }

  a {
    color: $charcoal-grey;
    text-decoration: none;
    cursor: pointer;
  }
}

.navbar-header {
  position: relative;
  text-align: center;

  .logo {
    width: 78%;
    margin-left: 11%;
    overflow: hidden;
    max-height: 56px;

    line-height: 56px;
    font-size: 20px;
  }

  .navbar-menu-pills {
    cursor: pointer;
    position: absolute;
    right: 10px;
    top: 0;
    line-height: 56px;
    height: calc(56px - 20px);
    width: calc(56px - 20px);

    font-size: 30px;
  }

  .navbar-menu-back {
    position: absolute;
    cursor: pointer;
    float: left;

    font-size: 30px;
    line-height: 56px;
    margin-left: 15px;
  }
}

.navbar-user {
  padding-bottom: 30px;
  border-bottom: solid 1px $white-three;

  > img {
    height: 56px;
    max-width: 56px;
    border-radius: 28px;
    float: left;
  }

  .navbar-info {
    width: calc(100% - 72px);
    float: right;
  }

  .navbar-user-title {
    font-size: 16px;
    font-weight: bold;
    color: $greyish-brown-two;
  }

  .navbar-user-subtitle {
    opacity: 0.5;
    font-size: 12px;
    font-weight: 500;
    color: $greyish-brown-two;
  }
}

li {
  cursor: pointer;
  position: relative;

  a {
    display: block;
    padding: 0 5px;
  }

  .fa {
    min-width: 15px;
    text-align: center;
    margin-right: 10px;
  }
}

.logout-container {
  position: absolute;
  left: 0;
  bottom: 20px;

  cursor: pointer;

  .fa {
    margin-right: 10px;
  }

  a {
    display: block;
  }
}

.notification-badge {
  position: absolute;
  top: 3px;
  right: -3px;
  height: 18px;
  min-width: 18px;
  padding: 0 4px;
  background-color: red;
  border-radius: 18px;
  color: #fff;
  font-size: 10px;
  line-height: 18px;
  text-align: center;
}
