@import "@/assets/styles/variables.scss";

.list-item {
  border-bottom: none;
}

.badge {
  &.draft,
  &.closed,
  &.waiting {
    background-color: $greyish-brown;
  }

  &.ongoing {
    background-color: $primary-color;
  }

  &.canceled,
  &.failure {
    background-color: $danger-color;
  }

  &.suspended,
  &.under_review {
    background-color: $peach;
  }

  &.approved,
  &.finnished,
  &.accepted {
    background-color: $apple-green;
  }
}

