@import "@/assets/styles/variables.scss";


.tab-item {
  height: 50px;
  float: left;
  padding-top: 5px;

  text-align: center;

  color: $pale-grey-two;
  cursor: pointer;
  border-bottom: solid 4px transparent;

  a {
    display: block;
    height: 100%;
    width: 100%;

    color: $pale-grey-two;
    text-decoration: none;
  }

  .tab-item-text {
    margin-top: -3px;
    font-size: 11px;
  }

  &.active {
    border-bottom-color: rgb(134, 190, 179);
  }
}

