@import "@/assets/styles/variables.scss";

.list-item {
  .list-span {
    margin-right: 10px;
  }
}

.badge {
  &.draft {
    background-color: $greyish-brown;
  }

  &.canceled,
  &.failure {
    background-color: $danger-color;
  }

  &.accepted {
    background-color: $apple-green;
  }

  &.desert {
    background-color: $peach;
  }

}
