@import "@/assets/styles/variables.scss";

.proposal-actions {
  text-align: center;

  .button {
    padding: 0 10px;
    margin-right: 10px;

    cursor: pointer;

    &:last-child {
      margin-right: 0;
    }
  }
}

.success-text {
  color: $apple-green;

  .fa-star {
    color: gold;
  }
}

.danger-text {
  color: $danger-color;
}

