@import "@/assets/styles/variables.scss";

  .list-item {
    min-height: 100px;

    background-color: #f7f7f7;
    cursor: pointer;
    font-size: 13px;

    &.read {
      background-color: transparent;
    }

    &:hover {
      background-color: rgba(0, 0, 0, 0.1);
    }

    .list-title {
      font-size: 15px;
    }

    .list-text {
      width: 85%;
      float: right;
      padding-right: 10px;

      text-decoration: none;
      color: $greyish-brown-two;
    }

    .list-icon {
      width: 15%;
      float: left;
      text-align: center;
      color: $dark-sky-blue;

      .far {
        margin-top: 10px;
      }
    }
  }

.blink-50 {
  .list-item {
    line-height: 120px;
  }
}

