@import "@/assets/styles/variables.scss";

$avatar: 40px;

ul {
  a {
    text-decoration: none;
    color: $greyish-brown-two;
  }
}

label {
  margin-right: 5px;
  font-size: 13px;
  font-weight: 300;
}

.blink-50 {
  color: $dark-grey-blue;

  .container {
    line-height: 109px;
    min-height: 109px;
  }
}

.badge {
  padding: 2px 10px;
  border-radius: 20px;

  color: $white;
  font-size: 12px;
  background-color: $primary-color;

  &.draft,
  &.closed,
  &.waiting {
    background-color: $greyish-brown;
  }

  &.ongoing {
    background-color: $primary-color;
  }

  &.canceled,
  &.failure {
    background-color: $danger-color;
  }

  &.suspended,
  &.under_review {
    background-color: $peach;
  }

  &.approved,
  &.finnished,
  &.accepted {
    background-color: $apple-green;
  }
}

.card {
  border: 0;
  box-shadow: 0 2px 3px 1px rgb(193, 218, 239);
  color: $dark-grey-blue;

  h5 {
    font-size: 14px;
    font-weight: bold;
  }
}

.card-description {
  font-size: 14px;
}

.root {
  height: calc(100vh - 56px);

  background-color: $background-color;
}

.user-card {
  padding: 10px;
  margin-top: -30px;
  border: none;

  > img {
    height: $avatar;
    max-width: $avatar;
    border-radius: 100%;
    float: left;
  }

  .user-info {
    padding-left: 10px;
    width: calc(100% - #{$avatar});
    float: right;

    color: $greyish-brown-two;
    font-size: 16px;

    line-height: 11px;

    span {
      font-size: 11px;
      color: $brownish-grey;
    }
  }
}

h6 {
  font-weight: 400;
  color: $dark-grey-blue;
}
