@import "@/assets/styles/variables.scss";


ul {
  list-style-type: none;
  list-style: none;
}

.search-field {
  position: relative;

  > .fa {
    cursor: pointer;
    position: absolute;
    right: 10px;
    top: 12px;
  }
}

.search-container-results {
  position: absolute;
  top: 40px;
  max-height: 60vh;
  overflow: scroll;

  font-size: 14px;
  background-color: white;

  li {
    border-bottom: solid 1px #e1e1e1;
    padding: 10px 0;
  }
}

.search-item-badge {
  display: inline-block;
  padding: 5px 10px;
  margin-bottom: 5px;
  margin-right: 5px;

  border-radius: 18px;
  background-color: $success-color;
  color: white;
  font-size: 14px;

  word-break: break-all;
}

.search-container {
  position: relative;
}

.hint {
  font-size: 75%;
  color: gray;
}

.search-button-container {
  position: fixed;
  left: 0;
  bottom: 0;
  text-align: center;

  .button {
    width: 90%;
  }
}

.search-container-wrapper {
  padding-bottom: 40px;
}


.search-selected-list {
  overflow: scroll;
  max-height: 370px;
}

