@import "@/assets/styles/variables.scss";

.badge {
  &.draft,
  &.closed,
  &.waiting {
    background-color: $greyish-brown;
  }

  &.ongoing {
    background-color: $primary-color;
  }

  &.canceled,
  &.failure {
    background-color: $danger-color;
  }

  &.suspended,
  &.under_review {
    background-color: $peach;
  }

  &.approved,
  &.finnished,
  &.accepted {
    background-color: $apple-green;
  }
}

.hint {
  color: gray;
  font-size: 80%;
}

